</template>
<script>
import Monthly from "./Monthly";
import Weekly from "./Weekly";
import Today from "./Today";
import Chart from './Chart'

export default {
    data() {
        return{
            month : false,
            week : false,
            day : true
        }
    },
    methods:{
        showmonth: function(){
            this.month= true
            this.week = false
            this.day = false
        },
        showweek: function(){
            this.month= false
            this.week = true
            this.day = false
        },
        showday: function(){
            this.month= false
            this.week = false
            this.day = true
        }
    },
    components: {
        Monthly,
        Weekly,
        Today,
        Chart,

        
    }
}
</script>
